import DeepData from "../../../assets/js/formate.js";
export default {
  data() {
    return {
      Title: "",
      ids: "",
      type_id: "",
      tableData: [],
      value: "",
      currentPage: 1,
      limit: 10,
      total: 0,
      visible: false,
      rules: {
        common: [{ required: true, message: "充值金额不合法", trigger: "blur" }]
      },
      form: {
        send_name: "",
        title: "",
        content: "",
        coins: "",
        uid: [],
        end_time: "",
        start_time: ""
      },
      title: "记录",
      disabled: false,
      disabledName: false
    };
  },
  created() {
    this.initdata({
      page: this.currentPage,
      limit: this.limit,
      mail_type: 1
    });
  },
  methods: {
    //写邮件
    writeEmail() {
      this.editForm("记录", true, false, false, {});
    },

    //搜索
    search() {
      if (this.ids === "") {
        this.ids = 0;
      } else if (this.type_id === "") {
        this.type_id = 0;
      }
      // console.log("chazhao", +this.type_id, +this.ids);
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        mail_type: 1,
        title: this.Title,
        ids: +this.ids,
        type_id: +this.type_id
      });
      this.ids = "";
      this.type_id = "";
      this.Title = "";
    },

    //表格编辑
    handleEdit(row) {
      // console.log(row);
      row = DeepData(row);
      row.start_time = this.data(row.start_time);
      row.end_time = this.data(row.end_time);
      this.editForm("编辑", true, false, true, row);
    },

    //表格详情
    detail(row) {
      row = DeepData(row);
      row.start_time = this.data(row.start_time);
      row.end_time = this.data(row.end_time);
      this.editForm("邮件详情", true, true, true, row);
    },

    //表格废弃邮件
    async del(row) {
      // console.log(row);
      row.send_status = "废弃";
      let resData = {
        mail_type: 1,
        send_status: 2,
        mail_id: row.id
      };
      let { data } = await this.$http.HallFunConfig.patchEmail(resData);
      // console.log(data);
      if (data.code === 1 && data.msg === "ok") {
        this.initdata({
          page: this.currentPage,
          limit: this.limit,
          mail_type: 1
        });
      }
    },

    //页容量变化
    handleSizeChange(num) {
      this.limit = num;
      this.currentPage = 1;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        mail_type: 1
      });
    },

    //页码变化
    handleCurrentChange(pagenum) {
      this.currentPage = pagenum;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        mail_type: 1
      });
    },

    //表单提交
    onSubmit(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (type === "记录") {
            // console.log(this.form);
            let resData = DeepData(this.form);
            resData.mail_type = 1;
            let { data } = await this.$http.HallFunConfig.PostEmail(resData);
            // console.log(data);

            if (data.code === 1 && data.msg === "ok") {
              this.initdata({
                page: this.currentPage,
                limit: this.limit,
                mail_type: 1
              });
            }
          } else if (type === "编辑") {
            // console.log(this.form);

            let res = {
              send_name: this.form.send_name,
              title: this.form.title,
              content: this.form.content,
              coins: this.form.coins,
              uid: this.form.uid,
              mail_type: 1,
              mail_id: this.form.id,
              start_time: this.form.start_time,
              end_time: this.form.end_time
            };
            let { data } = await this.$http.HallFunConfig.PutEmail(res);
            // console.log(data);

            if (data.code === 1 && data.msg === "ok") {
              this.initdata({
                page: this.currentPage,
                limit: this.limit,
                mail_type: 1
              });
            }
          } else if (type === "邮件详情") {
          }

          this.editForm("记录", false, false, false, {});
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    editForm(title, visible, disabled, disabledName, form) {
      this.title = title;
      this.visible = visible;
      this.disabled = disabled;
      this.disabledName = disabledName;
      this.form = form;
    },

    formateData(res) {
      res.forEach(item => {
        let start_time = this.data(item.start_time);
        let end_time = this.data(item.end_time);
        let currentTime = Date.parse(new Date());
        // console.log(start_time, end_time, currentTime);

        if (currentTime < start_time) {
          item.send_status = "未生效";
        } else if (currentTime > start_time && currentTime < end_time) {
          item.send_status = "生效中";
        } else if (currentTime > end_time) {
          item.send_status = "已过期";
        }
      });
      return res;
    },

    data(time) {
      let long1 = Date.parse(time);
      let long2 = new Date(long1).getTime();
      return long2;
    },

    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetEmail(params);
      let fres = this.formateData(data.data);
      this.tableData = fres;
      this.total = data.total;
      if (this.total === 0) {
        this.$message({
          type: "warning",
          message: "没有找到合适的数据!"
        });
      }
      // console.log(data);
    }
  }
};



